.headline {
    display: flex;
    align-items: center;
    padding-top: 19px;
}

.logo {
    opacity: 0;
    animation: Show 0.4s 0.1s linear;
    animation-fill-mode: forwards;
}

@keyframes Show {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

.button {
    opacity: 0;
    animation: Show 0.2s 0.4s linear;
    animation-fill-mode: forwards;
}