.button {
    display: inline-flex;
    justify-content: center;
    align-items: flex-start;

    min-width: 144px;
    height: 46px;

    padding: 13px 30px;

    font-family: 'Inter', sans-serif;
    font-size: 16px;
    font-weight: 600;
    letter-spacing: 0.02em;

    background: rgba(58, 110, 232, 0.2);
    border: 2px solid #3A6EE8;
    box-sizing: border-box;
    border-radius: 5px;

    color: #fff;
}