.tags {
    display: flex;

    list-style-type: none;

    margin-top: auto;
    margin-bottom: 0;

    padding: 0;

    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 150%;

    opacity: 0.8;
}

.tags li {
    margin-right: 22px;
}

.tags li:last-child {
    margin-right: 0;
}

.tags a {
    color: #FFF;
    text-decoration: none;
}