.wrapper {
    position: relative;

    width: 100%;

    padding-bottom: 53.6%;

    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}

.button {
    position: absolute;

    top: 50%;
    left: 50%;

    border: none;
    background-color: transparent;

    transform: translate(-50%, -50%);

    cursor: pointer;
}

.button img {
    display: block;
    margin: 0;
}