.wrapper {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.circle {
    position: relative;

    width: 88px;
    height: 88px;

    margin-bottom: 11px;

    border-radius: 50%;

    border: 1px solid #3A6EE8;
}

.circle:hover ~ .description {
    opacity: 1;
}

.circle:hover ~ .description span:last-child {
    opacity: 0.6;
}

.circle:hover .waves::before {
    opacity: 0.2;
}

.circle:hover .waves::after {
    opacity: 0.05;
}

.waves {
    position: absolute;

    top: 50%;
    left: 50%;

    transform: translate(-50%, -50%);
}

.waves::before,
.waves::after {
    content: '';

    position: absolute;

    top: 50%;
    left: 50%;

    border: 1px solid #3A6EE8;
    border-radius: 50%;

    transform: translate(-50%, -50%);

    opacity: 0;
}

.waves::before {
    width: 116px;
    height: 116px;    
}

.waves::after {
    width: 156px;
    height: 156px;
}

.circleActive::after {
    background-color: #3A6EE8;
}

.circle::after {
    content: '';

    position: absolute;

    top: 50%;
    left: 50%;

    width: 60px;
    height: 60px;

    border-radius: 50%;

    transform: translate(-50%, -50%);
}

.dot {
    position: absolute;

    width: 8px;
    height: 8px;

    border-radius: 50%;

    background: #3A6EE8;
}

.title {
    width: 120px;

    margin-top: 0;
    margin-bottom: 0;

    font-weight: normal;
    font-size: 22px;
    line-height: 90%;

    text-align: center;

    color: rgba(255, 255, 255, 0.4);
}

.description {
    position: absolute;

    top: 113px;
    left: 50%;

    height: 108px;
    width: 473px;

    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 150%;

    text-align: center;

    color: #FFF;

    opacity: 0;
    border: 1px solid #000000;
    text-shadow: 0px 0px 50px #171717;

    transform: translateX(-50%);
}

.line {
    position: absolute;
    background-repeat: no-repeat;
}
