.row {
    display: flex;
}

.row:first-child .column:nth-child(odd) {
    background-color: #1A1E2D;
}

.row:first-child .column:nth-child(even) {
    background-color: #162237;
}

.row:last-child .column:nth-child(odd) {
    background-color: #162237;
}

.row:last-child .column:nth-child(even) {
    background-color: #1A1E2D;
}

.column {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 230px;
}

.column img {
    display: block;
    margin-bottom: 16px;
}

.column p {
    margin-top: 0;

    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 100%;
    letter-spacing: 0.02em;

    color: #D8E2FF;
}
