.lalala {
    opacity: 0;
    position: fixed;
    top: 100%;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(84, 152, 255, 0.3);
    animation: MoveUp 1s linear;
}

.lalala::after {
    position: fixed;
    content: '';
    top: 100%;
    left: 0;
    width: 100%;
    heighT: 100%;
    background-color: #5498FF;
    animation: MoveUp 0.7s 0.3s linear;
}

@keyframes MoveUp {
    0% {
      top: 100%;
    }
    100% {
      top: -100%;
    }
}

@keyframes MoveUpAfter {
    0% {
        top: 100%;
    }
    100% {
        top: -100%;
    }
}