.wrapper {
    display: flex;
    align-items: center;

    font-weight: 500;
    font-size: 18px;

    line-height: 130%;
    text-align: right;

    color: #FFF;

    opacity: 0;
    animation: Show 0.2s 0.4s linear;
    animation-fill-mode: forwards;
}

.wrapper span {
    margin-right: 10px;
}

@keyframes Show {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}
