.wrapper {
    position: relative;

    display: flex;

    justify-content: flex-start;
    align-items: center;

    width: 363px;
    height: 363px;

    margin: 0 auto;

    text-align: center;

    border: 1px solid rgba(58, 110, 232, 0.4);
    border-radius: 50%;
}

.wrapper::after {
    z-index: 1;
    content: '';
    position: absolute;
    top: 0;
    bottom: -1px;
    left: -1px;
    right: -1px;
    background-image: linear-gradient(0deg, #000 50%, transparent);
}

.inner {
    width: 321px;
    height: 321px;

    margin: 0 auto;

    border: 1px solid #3A6EE8;
    border-radius: 50%;
}

.title {
    padding-top: 35px;

    font-size: 24px;
    font-weight: bold;
    line-height: 90%;
}

.wrapper:hover .description {
    opacity: 1;
}

.wrapper:hover .description span:last-child {
    opacity: 0.6;
}

.description {
    z-index: 2;

    opacity: 0;

    position: absolute;

    top: 113px;
    left: 50%;

    height: 108px;
    width: 473px;

    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 150%;

    text-align: center;

    color: #FFF;

    opacity: 0;
    border: 1px solid #000000;
    text-shadow: 0px 0px 50px #171717;

    transform: translateX(-50%);
}
