.three {
    max-width: 1210px;
    margin: 0 auto;
    margin-bottom: 25px;
}

.nodeRoot {
    display: flex;
    justify-content: center;
}

.node {
    display: flex;
    flex-direction: column;
    margin-bottom: 10px;
}

.node + .node {
    margin-left: 30px;
}

.nodeMiddle {
    margin: 0 auto;
    padding-top: 140px;
}

.row {
    display: flex;
    justify-content: center;
}

.children {
    display: flex;
    padding-top: 50px;
}
