.title {
    margin-bottom: 48px;

    font-family: 'Autobus Bold';
    font-size: 88px;
    font-weight: bold;
    line-height: 100%;

    letter-spacing: 0.1em;

    text-shadow:
        1px 0 0 #fff,
        -1px 0 0 #fff,
        0 1px 0 #fff,
        0 -1px 0 #fff,
        
        1px 1px #fff,
        -1px -1px 0 #fff,
        1px -1px 0 #fff,
        -1px 1px 0 #fff;


    text-align: center;
    text-transform: uppercase;

    color: #fff;
}