.headline {
    position: relative;
    max-width: 1200px;
}

.h1 {
    display: flex;
    flex-direction: column;

    max-width: 800px;
    margin: 0;

    font-family: 'Inter', sans-serif;

    font-style: normal;
    font-weight: bold;
    font-size: 100px;
    line-height: 0.91;

    text-transform: uppercase;

    color: #FFF;
}

.h1 > span {
    overflow: hidden;
}

.h1 > span > span {
    display: block;
    position: relative;
    top: 80px;
    transform: rotate(3deg);
    animation-fill-mode: forwards;
}

.h1 > span:nth-child(1) > span {
    animation: MoveUp 0.4s ease-in-out;
    animation-fill-mode: forwards;
}

.h1 > span:nth-child(2) > span {
    animation: MoveUp 0.4s 0.1s ease-in-out;
    animation-fill-mode: forwards;
}

.h1 > span:nth-child(3) > span {
    animation: MoveUp 0.4s 0.15s ease-in-out;
    animation-fill-mode: forwards;
}

@keyframes MoveUp {
    0% {
      top: 80px;
      transform: rotate(3deg);
    }
    100% {
      top: 0;
      transform: rotate(0deg);
    }
}

.h1 span:nth-child(2) span {
    color: white; /* Unfortunately you can't use transparent here … */
    text-shadow:
     -1px -1px 0 #fff,  
    1px -1px 0 #fff,
    -1px 1px 0 #fff,
     1px 1px 0 #fff
}

@supports((text-stroke: 2px #fff) or (-webkit-text-stroke: 2px #fff)) {
    .h1 span:nth-child(2) span {
        color: transparent;
        -webkit-text-stroke: 2px #fff;
        text-stroke: 2px #fff;
        text-shadow: none;
    }
}

.description {
    position: absolute;

    top: 182px;
    left: 455px;

    width: 400px;

    font-family: 'Inter', sans-serif;
    font-weight: 500;
    font-size: 18px;
    line-height: 130%;

    white-space: pre;

    color: #FFF;

    opacity: 0;

    animation: Show 0.4s 0.4s ease-in-out;
    animation-fill-mode: forwards;
}

@keyframes Show {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

.description span {
    display: block;
}
