.wrapper {
    padding: 80px 0 160px;
}

.movies {
    margin-bottom: 60px;
}

.row {
    display: flex;
    margin-bottom: 20px;
}

.column {
    width: 33.333%;
    margin-right: 20px;
}

.column:last-child {
    margin-right: 0;
}

.showMore {
    position: relative;

    display: flex;
    justify-content: space-between;

    width: calc(33.333% - 15px);

    margin: 0 auto;
    padding: 0;
    padding-bottom: 10px;

    border: none;

    border-bottom: 2px solid rgba(255, 255, 255, 0.2);

    background-color: transparent;

    cursor: pointer;
}

.showMoreText {
    font-family: 'Trap';
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    line-height: 90%;

    color: #FFF;
}

.showMoreUnderline {
    position: absolute;
    bottom: 0;
    left: 0;
    height: 2px;
    background-color: #FFF;
}