.wrapper {
    flex-shrink: 0;
}

.img {
    margin-bottom: 2px;
}

.title {
    margin-top: 0;
    margin-bottom: 1px;

    font-family: 'Autobus Bold';
    font-style: normal;
    font-weight: bold;
    font-size: 24px;
    line-height: 100%;
    letter-spacing: 0.1em;
    text-transform: uppercase;

    text-shadow:
        1px 0 0 #5498FF,
        -1px 0 0 #5498FF,
        0 1px 0 #5498FF,
        0 -1px 0 #5498FF,
        
        1px 1px #5498FF,
        -1px -1px 0 #5498FF,
        1px -1px 0 #5498FF,
        -1px 1px 0 #5498FF;


    color: #5498FF;
}

.position {
    margin-top: 0;
    margin-bottom: 5px;

    font-family: 'Inter', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 160%;
    letter-spacing: 0.02em;

    color: #DEDEFD;

    opacity: 0.6;
}

.contacts {
    list-style-type: none;

    margin-top: 0;
    padding-left: 8px;

    font-family: 'Inter', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 160%;
    letter-spacing: 0.02em;

    color: #FFF;
}

.contact {
    position: relative;

    margin-bottom: 3px;

    padding-left: 15px;
}

.contact::before {
    content: '';
    position: absolute;
    top: 11px;
    left: 0;
    width: 5px;
    height: 5px;
    border-radius: 50%;
    background-color: #fff;
}

.cotnact:last-child {
    margin-bottom: 0;
}
