.wrapper {
    display: flex;
}

.group {
    margin-right: 100px;
}

.group:last-child {
    margin-right: 0;
}

.title {
    margin-top: 0;
    margin-bottom: 20px;

    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    line-height: 150%;
}

.menu {
    list-style-type: none;
    margin-bottom: 0;
    padding-left: 0;
}

.menuItem {
    margin-bottom: 15px;

    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 150%;

    opacity: 0.6;
}

.menuItem:last-child {
    margin-bottom: 0;
}

.menuItem a {
    color: #FFF;
    text-decoration: none;
}